.portfolio-home {
    display: flex;
    flex-direction: column-reverse;
    box-sizing: border-box;

    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}
.background-image {
    box-sizing: border-box;
    position: relative;
    object-fit: cover;
    width: 100%;
    height: 90vh;
}
.home-greetings {
    display: flex;
    flex-direction: column;
    position: absolute;
    gap: 0.5rem;
    margin: 4rem 5rem;

    font-size: 3rem;
    color: var(--white);
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.546);
}
.greetings-title {
    line-height: 2rem;
}
.greetings-title strong {
    font-size: 2rem;
    font-weight: 100;
    color: var(--green-500);
}
.greetings-title h2 {
    font-size: 2.5rem;
    font-weight: bolder;
}
.greetings-title p {
    font-size: 1rem;
}

.greetings-buttons {
    display: flex;
    flex-direction: row;
    gap: 1rem;
}
.project-button,
.curriculum-button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;

    line-height: 0;
    font-size: 0.8rem;
    padding: 0.5rem 2rem;

    color: var(--white);
    border: 1px solid var(--green-500);
    background-color: transparent;
}
.project-button {
    background-color: var(--green-700);
    animation-name: hoverEffect;
}
.curriculum-button,
.curriculum-button:visited {
    color: var(--green-500);
}
.project-button:visited {
    color: var(--white);
}
.curriculum-button:hover,
.project-button:hover {
    transform: scale(1.05);
}
@media (max-width: 480px) {
    .portfolio-home {
        flex-direction: column;
        justify-items: center;
        align-items: center;
    }
    .home-greetings {
        gap: 0.5rem;
        margin: 0 2rem;
        top: 50%;
    }
    .greetings-title strong {
        font-size: 1rem;
        line-height: 1rem;
    }
    .greetings-title h2 {
        font-size: 2rem;
        line-break: loose;
    }
    .greetings-title p {
        line-height: 1.4rem;
    }
    .project-button {
        line-height: normal;
        font-size: 1rem;
        padding: 0.5rem 2rem;
    }
    .curriculum-button {
        display: none;
    }
}
